@import './../../styles/mixins.module';
@import './../../styles/vars.module';

.loginBlock {
  display: flex;
  justify-content: center;

  .inner {
    margin-top: 3%;
    padding: 20px;
    width: 360px;
    height: 100%;
    background-color: #fff;
    color: rgba(0, 0, 0, 0.87);
    text-align: center;
    border-radius: 4px;
    box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%),
      0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);
    h3 {
      font-weight: bold;
      font-size: 21px;
    }

    .textBlock{
      padding: 15px;

      .Text{
        padding-top: 15px;
        font-size: 14;
        color: #4A5571;
      }
      .Title{
        color: $btnHover;
        padding-top: 10px;
        font-size: 26px;
        font-weight: bold;
      }
    }

    .redirectBlock {
      display: flex;
      flex-direction: column;
      padding: 5px;
      gap: 10px;

      span {
        font-size: 20px;
      }
      a {
        color: black;
      }
      .signBtn {
        color: rgb(52, 106, 255);
      }
    }
  }
}

.link {
  color: black;
}

.form {
  margin-top: 20px;
  padding: 5px;

  .inputWrap {
    margin-bottom: 18px;

    .input {
      width: 100%;
    }
  }
  .btnWrap {
    margin-top: 30px;

    .formBtn {
      width: 100%;
      height: 44px;
      background-color: $MainColor;
      font-size: 16px;
    }
    .formBtn:hover{
      background-color: $btnHover;
    }
  }
}
