@import './../../../styles/mixins.module';
@import './../../../styles/vars.module';
.interestsCard {
  padding: 20px;

  @include flexEdit(center, center);

  .inner {
    transform: translateY(-20px);
    text-align: center;

    .applyButton{
      background-color: $MainColor;
    }

    .checkboxes {
      margin: 20px 0 20px;

      .inputLabel {
        margin: 1px;
        padding: 5px;
        font-size: 16px;
        border-radius: $borderRadius;
        color: #fff;
      }
    }
  }
}